import React, { useState } from 'react'
import logo from '../../assets/img/logo.png';
import avatar from '../../assets/img/company.jpg';
import '../../assets/css/template.css'
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import '../../assets/css/sidebar.css'
import { Link , Outlet, useNavigate} from 'react-router-dom';
import Cookies from 'universal-cookie';


export default function Navbar() {

   const [toogle, setToogle] = useState(false);
   const cookies = new Cookies();
   const navigate = useNavigate();

   const logout = () => {
      cookies.remove('Username')
      cookies.remove('Id')
      navigate('/')
   }

  
  return (
    
    <div className={toogle ? 'toggle-sidebar' : ''}>        
        <header id="header" className="header fixed-top d-flex align-items-center">
         <div className="d-flex align-items-center justify-content-between"> <span className="logo"> <img src={logo} alt=""/> </span> <i className="bi bi-list toggle-sidebar-btn" onClick={() => setToogle(!toogle)}></i></div>
         <div className="search-bar">
            <form className="search-form d-flex align-items-center" method="POST" action="#"> <input type="text" name="query" placeholder="Search" title="Enter search keyword" /> <button type="submit" title="Search"><i className="bi bi-search"></i></button></form>
         </div>
         <nav className="header-nav ms-auto">
            <ul className="d-flex align-items-center">
               <li className="nav-item d-block d-lg-none"> <span className="nav-link nav-icon search-bar-toggle " href="#"> <i className="bi bi-search"></i> </span></li>
               <li className="nav-item dropdown pe-3">
                  <span className="nav-link nav-profile d-flex align-items-center pe-0" href="#" data-bs-toggle="dropdown"> <img src={avatar} alt="Profile" className="rounded-circle img-user"/> <span className="d-none d-md-block dropdown-toggle ps-2">{cookies.get('Name')}</span> </span>
                  <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow profile">
                     <li className="dropdown-header">
                        <h6>{cookies.get('Name')}</h6>
                        <span>{cookies.get('Username')}</span>
                     </li>
                     <li>
                        <hr className="dropdown-divider"/>
                     </li>
                     <li> <Link to='/navbar/user'><span className="dropdown-item d-flex align-items-center"> <i className="bi bi-person"></i> <span>Profile</span> </span></Link></li>
                     <li>
                        <hr className="dropdown-divider"/>
                     </li>
                     {/* <li> <a className="dropdown-item d-flex align-items-center" href="users-profile.html"> <i className="bi bi-gear"></i> <span>Account Settings</span> </a></li> */}
                     <li>
                        <hr className="dropdown-divider"/>
                     </li>
                     <li> <a className="dropdown-item d-flex align-items-center" href="pages-faq.html"> <i className="bi bi-question-circle"></i> <span>Besoin d'aide?</span> </a></li>
                     <li>
                        <hr className="dropdown-divider"/>
                     </li>
                     <li onClick={() => logout()} className="logout-btn"> <span className="dropdown-item d-flex align-items-center"> <i className="bi bi-box-arrow-right"></i> <span>Deconnexion</span> </span></li>
                  </ul>
               </li>
            </ul>
         </nav>
      </header>
      <aside id="sidebar" className='sidebar'>
         <ul className="sidebar-nav" id="sidebar-nav">
            
            <li className="nav-item"> 
                <Link to="/navbar/dashboard-client" > 
                    <span className="nav-link collapsed" href="index.html"> <i className="bi bi-grid"></i> <span>Dashboard</span> </span>
                </Link>
            </li>
            <li className="nav-heading">Pages</li>
            <li className="nav-item"> 
               <Link to="/navbar/projet" > 
                  <span className="nav-link collapsed"> <i className="bi bi-folder-check"></i> <span>Projets</span> </span>
               </Link>
            </li>
            <li className="nav-item">
               <span className="nav-link collapsed" data-bs-target="#tables-nav" data-bs-toggle="collapse" href="#"> <i className="bi bi-layout-text-window-reverse"></i><span>Salaris</span><i className="bi bi-chevron-down ms-auto"></i> </span>
               <ul id="tables-nav" className="nav-content collapse " data-bs-parent="#sidebar-nav">
                  <li>
                    <Link to="/navbar/addsalarie" > 
                        <i className="bi bi-circle"></i><span>Nouveau</span>
                    </Link>
                  </li>
                  <li>
                    <Link to="/navbar/salarie" >  
                      <i className="bi bi-circle"></i><span>Liste</span>
                    </Link>
                  </li>
               </ul>
            </li>
            <li className="nav-item"> 
               <Link to="/navbar/participer" > 
                  <span className="nav-link collapsed"> <i className="bi bi-bezier2"></i> <span>Contribution</span> </span>
               </Link>
            </li>
            <li className="nav-item"> 
               <Link to="/navbar/user" > 
                  <span className="nav-link collapsed"> <i className="bi bi-gear"></i> <span>Parametre</span> </span>
               </Link>
            </li>
            <li className="nav-item"> 
               <Link to="/navbar/contact" > 
                  <span className="nav-link collapsed"> <i className="bi bi-envelope"></i> <span>Contact</span> </span>
               </Link>
            </li>
         </ul>
      </aside>
      {/* <div className='contenu'>
        fdfdfdfd
      </div> */}
      <span className="back-to-top d-flex align-items-center justify-content-center"><i className="bi bi-arrow-up-short"></i></span>  
        <Outlet />
    </div>
  )
}
