import axios from 'axios';
import { HttpRequestHeader } from '../helpers/utils';
export const getUser = () => {
    return axios.get('http://localhost:5000/utilisateurs' , {
        headers: HttpRequestHeader
    });
}

export const updateUser = (id, user) => {
    return axios.patch('http://localhost:5000/utilisateurs' + id, user , {
        headers: HttpRequestHeader
    });
}

export const storeUser = (user) => {
    return axios.post('http://localhost:5000/utilisateurs' , user , {
        headers: HttpRequestHeader
    });
}