import React, { useEffect, useState } from 'react'
// import { getSalaries } from "../../services/salarie.service";
// import { DataGrid } from '@mui/x-data-grid';
import { salariesdata } from '../../helpers/utils';
import vide from '../../assets/img/nodata.jpg'
import Button from '@mui/material/Button';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import DoneAllIcon from '@mui/icons-material/DoneAll';
import { FormControl, InputLabel, MenuItem, Paper, Select, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField } from '@mui/material';
import { storeSalarie } from '../../services/salarie.service';
import Cookies from 'universal-cookie';


export default function Salarie() {

    const [data, setData] = useState([]);

    const cookies = new Cookies();

    const [small_id, setSmall_id] = useState('');
    const [nom, setNom] = useState('');
    const [prenom, setPrenom] = useState('');
    const [adresse, setAdresse] = useState('');
    const [contact, setContact] = useState('');
    const [diplome, setDiplome] = useState('');
    const [etablissement, setEtablissement] = useState('');
    const [annee, setAnnee] = useState('');
    const [fonction, setFonction] = useState('');
    const [brut, setBrut] = useState('');
    const [cotisation, setCotisation] = useState('');
    const [cadre, setCadre] = useState('');
    const [cheure, setCheure] = useState('');
    const [entrer, setEntrer] = useState('');
    const [sortie, setSortie] = useState('');
    const [ticket, setTicket] = useState('');
    
 

    useEffect(() => { 
        setData([...salariesdata])
    },[]);

    const onRemove =(id) =>{
        console.log(id)
        var index = salariesdata.findIndex((e) => {
			return e.id === id;
		});
        if (index !== -1) {
            salariesdata.splice(index, 1);
        //   console.log('array : '+array)
          setData([...salariesdata]);
          console.log('salarie data : '+salariesdata)
        }
    }

      const onOpenModal = (id) =>{
        console.log(id)
        var index = salariesdata.findIndex((e) => {
			return e.id === id;
		});
        if (index !== -1) {
            console.log(salariesdata[index])
            setSmall_id(salariesdata[index].id)
            setNom(salariesdata[index].nom)
            setPrenom(salariesdata[index].prenom)
            setAdresse(salariesdata[index].adresse)
            setContact(salariesdata[index].contact)
            setDiplome(salariesdata[index].diplome);
            setEtablissement(salariesdata[index].etablissement);
            setAnnee(salariesdata[index].annee);
            setFonction(salariesdata[index].fonction);
            setBrut(salariesdata[index].brut);
            setCadre(salariesdata[index].cadre);
            setCotisation(salariesdata[index].cotisation);
            setCheure(salariesdata[index].cheure);
            setEntrer(salariesdata[index].entrer);
            setSortie(salariesdata[index].sortie);
            setTicket(salariesdata[index].ticket);
        }
      }

      const onSubmit = (e) => {
          let salarie = {
            id : small_id,
            nom : nom,
            prenom : prenom,
            adresse : adresse,
            contact : contact,
            diplome : diplome,
            etablissement : etablissement,
            annee : annee,
            fonction : fonction,
            cadre : cadre,
            brut : brut,
            cotisation : cotisation,
            cotisationeligible : ((cotisation * 20) / 100).toFixed(2),
            salairecharge : (cotisation * 20) / (100 * brut),
            cheure : cheure,
            cjours : (cheure / 24).toFixed(2),
            entrer : entrer,
            sortie : sortie,
            ticket : ticket,
            user_id : cookies.get('Id')

          };
          salariesdata.push(salarie)
          setData([...salariesdata]);
          console.log('salarie modifier',salarie)
        //   salariesdata.concat(data);
          console.log('Liste salaris' , salariesdata)
      }
      
      const onUpdate = (id) => {
        onRemove(id);
        onSubmit();
      }
      const onValide = () => {
        data.map((elem) => ( 
            storeSalarie(elem)
            .then((result) => {
                return result.data
            })
            ).catch((error) => {
                console.error(error);
            })
        )
        setData([]);
      }
        return (
            <div id="main">
                <div className='salarie-content'>                    
                    <div className="nouveau d-flex">
                            <div className="nouveau-text d-flex">
                                <span>
                                    <h1><FactCheckIcon fontSize='100' />&ensp;</h1>
                                </span>
                                <span>
                                    <span className="nouveau-title">Liste des salaris que vous avez ajout</span><br/>
                                    <span className="nouveau-desc">Cliquer sur le boutton valider pour enregistrer la liste</span>
                                </span>
                            </div>
                            <div className="nouveau-boutton">
                                <Button onClick={() => onValide()} variant="contained" color="primary" size="large" startIcon={<DoneAllIcon />} disabled={ data.length == null || data.length === 0 ? true : false}>
                                    VALIDER
                                </Button>
                            </div>
                    </div><br/>
        
                    <div className='listSalarie'>
                        <div className="modal fade" id="ExtralargeModal" tabIndex="-1">
                            <div className="modal-dialog modal-xl modal-dialog-scrollable">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title">Information sur le salari</h5>
                                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                    </div>
                                    <div className="modal-body"> 
                                        <div className='row'>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="nom" label="Nom" value={nom} onChange={(e) => setNom(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="prenom" label="Prnom(s)" value={prenom} onChange={(e) => setPrenom(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="adresse" label="Adresse" value={adresse} onChange={(e) => setAdresse(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="contact" label="Contact" value={contact} onChange={(e) => setContact(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="diplome plus lev" label="Diplme" value={diplome} onChange={(e) => setDiplome(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="etablissement" label="Etablissement" value={etablissement} onChange={(e) => setEtablissement(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="annee" label="Anne d'obtention" value={annee} onChange={(e) => setAnnee(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="fonction" label="Fonction" value={fonction} onChange={(e) => setFonction(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="brut" label="Salaire brut" value={brut} onChange={(e) => setBrut(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth margin="normal" id="outlined-basic" name="cotisation" label="Cotisation totales" value={cotisation} onChange={(e) => setCotisation(e.target.value)} variant="outlined" size="small"/>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <FormControl fullWidth  margin="normal">
                                                    <InputLabel id="demo-simple-select-label">Employ cadre</InputLabel>
                                                    <Select
                                                        labelId="demo-simple-select-label"
                                                        id="demo-simple-select"
                                                        value={cadre}
                                                        label="Employ cadre"
                                                        size='small'
                                                        onChange={(e) => setCadre(e.target.value)}
                                                        
                                                        // sx={{width : 200}}
                                                    >
                                                        <MenuItem value='oui'>Oui</MenuItem>
                                                        <MenuItem value='non'>Non</MenuItem>
                                                    </Select>
                                                </FormControl>
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth id="outlined-basic" margin='normal' size='small' label="Cumul temps (en heures)" variant="outlined" value={cheure} onChange={(e) => setCheure(e.target.value)} />
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField fullWidth id="outlined-basic" margin='normal' size='small' label="Part/Int & Tickets restaurants" variant="outlined" value={ticket} onChange={(e) => setTicket(e.target.value)} />
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField
                                                    fullWidth
                                                    size='small'
                                                    id="date"
                                                    label="Entrer"
                                                    type="date"
                                                    margin='normal'
                                                    value={entrer} 
                                                    onChange={(e) => setEntrer(e.target.value)}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }}
                                                />
                                            </div>
                                            <div className='col-sm-6 col-md-4 col-lg-3'>
                                                <TextField
                                                    fullWidth
                                                    size='small'
                                                    id="date"
                                                    label="Sortie"
                                                    type="date"
                                                    margin='normal'
                                                    value={sortie} 
                                                    onChange={(e) => setSortie(e.target.value)}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="modal-footer"> <button type="button" className="btn btn-secondary" data-bs-dismiss="modal"><i className='bi bi-reply-all-fill'> </i> Annuler</button> <button type="button" className="btn btn-primary" data-bs-dismiss="modal" onClick={() => onUpdate(small_id)}><i className='bi bi-check2-circle'> </i> Enregister</button></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {data.length == null || data.length === 0 ? (
                        <div className='text-vide text-center'>
                            <h2>Vous n'avez pas encore entrer des salaris!</h2>
                            <img src={vide} alt="no data" className='nodata-img' width='40%'/>
                        </div> ) : ( 
                            <div className='row'>
                            <TableContainer component={Paper}>
                                <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                                    <TableHead> 
                                    <TableRow> 
                                        <TableCell>Nom</TableCell>
                                        <TableCell>Prnom(s)</TableCell>
                                        <TableCell>Fonction</TableCell>
                                        <TableCell>Brut</TableCell>
                                        <TableCell>Cotisation</TableCell>
                                        <TableCell>Temps(h)</TableCell>
                                        <TableCell>Action</TableCell>
                                    </TableRow>
                                    </TableHead>
                                    <TableBody>
                                    {data.map((elem) => (
                                        <TableRow
                                        key={elem.id}
                                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <TableCell component="th" scope="row">
                                                {elem.nom}
                                            </TableCell>
                                            <TableCell>{elem.prenom}</TableCell>
                                            <TableCell>{elem.fonction}</TableCell>
                                            <TableCell>{elem.brut}</TableCell>
                                            <TableCell>{elem.cotisation}</TableCell>
                                            <TableCell>{elem.cheure}</TableCell>
                                            <TableCell>
                                                <i className='close-list-item edit bi bi-pencil' onClick={() => onOpenModal(elem.id)} data-bs-toggle="modal" data-bs-target="#ExtralargeModal"></i>&ensp;&ensp;
                                                <i className='close-list-item remove bi bi-x-circle-fill' onClick={() => onRemove(elem.id)}></i>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            {/* {data.length == null || data.length === 0 ? (
                            <div className='text-vide text-center'>
                                <h2>Vous n'avez pas encore entrer des salaris!</h2>
                                <img src={vide} alt="no data" className='nodata-img' width='40%'/>
                            </div> ) : (data.map(e => (
                                <div className='col-sm-12 col-md-6 col-lg-4 col-xl-3 mb-5 listSalarie-item'>
                                        
                                    <MDBCard>
                                        <MDBCardBody>
                                            <MDBCardHeader>
                                                <span className='close-list'> 
                                                    <i className='close-list-item bi bi-pencil' onClick={() => onOpenModal(e.id)} data-bs-toggle="modal" data-bs-target="#ExtralargeModal"></i> 
                                                    <i className='close-list-item bi bi-x-circle-fill' onClick={() => onRemove(e.id)}></i>
                                                </span>  
                                            </MDBCardHeader>
                                            <MDBCardText muted>
                                                <span>Nom: <b>{e.prenom} {e.nom}</b></span><br/>
                                                <span>Adresse: <b>{e.adresse}</b></span><br/>
                                                <span>Contact: <b>{e.contact}</b></span><br/>
                                                <span>Diplme plus lev: <b>{e.diplome}</b></span><br/>
                                                <span>Etablissement: <b>{e.etablissement}</b></span><br/>
                                                <span>Anne d'obtention: <b>{e.annee}</b></span><br/>
                                                <span>Fonction: <b>{e.fonction}</b></span><br/>
                                                <span>Salaire brut: <b>{e.brut}</b></span><br/>
                                                <span>Employ cadre: <b>{e.cadre}</b></span><br/>
                                                <span>Cotisation totales: <b>{e.cotisation}</b></span><br/>
                                                <span>Cotisation eligible: <b>{e.cotisationEligible}</b></span><br/>
                                                <span>Salaire charg: <b>{e.salaireCharge}</b></span><br/>
                                                <span>Tickets restaurants: <b>{e.ticket}</b></span><br/>
                                                <span>Cumul temps en heures: <b>{e.cheure}</b> H </span><br/>
                                                <span>Cumul temps en jours: <b>{e.cjours}</b> J </span><br/>
                                                <span>Participation en projet: <b>{e.participation}</b></span><br/>
                                                <span>Date d'entrer: <b>{e.entrer}</b></span><br/>
                                                <span>Date de sortie: <b>{e.sortie}</b></span><br/>
    
                                            </MDBCardText>
                                        </MDBCardBody>
                                    </MDBCard>  
                                </div>
                                )))
                            } */}
                        </div>
                        )}
                    

                </div>
            </div>
        )
        
    }

