import React, { useEffect } from 'react'
import avatar from '../../assets/img/company.jpg';
import Cookies from 'universal-cookie';
import { useState } from 'react';


export default function User() {

      useEffect(() => {
            
      }, []);
      const cookies = new Cookies();

      const [name, setName] = useState(cookies.get('Name'));
      const [secteur, setSecteur] = useState(cookies.get('Secteur'));
      const [about, setAbout] = useState(cookies.get('About'));
      const [email, setEmail] = useState(cookies.get('Email'));
      const [contact, setContact] = useState(cookies.get('Contact'));
      const [adresse, setAdresse] = useState(cookies.get('Adresse'));
      const [username, setUsername] = useState(cookies.get('Username'));
      const [password, setPassword] = useState(cookies.get('Password'));

      

    return (
        <div id="main">
            <section className="section profile profile-section">
            <div className="row">
               <div className="col-xl-4">
                  <div className="card">
                     <div className="card-body profile-card pt-4 d-flex flex-column align-items-center">
                        <img src={avatar} alt="Profile" className="rounded-circle"/>
                        <h2>{cookies.get('Name')}</h2>
                        <h3>{cookies.get('Username')}</h3>
                     </div>
                  </div>
               </div>
               <div className="col-xl-8">
                  <div className="card contact">
                     <div className="card-body pt-3 php-email-form">
                        <ul className="nav nav-tabs nav-tabs-bordered">
                           <li className="nav-item"> <button className="nav-link active" data-bs-toggle="tab" data-bs-target="#profile-overview">Information</button></li>
                           <li className="nav-item"> <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-edit">Modifier profile</button></li>
                           {/* <li className="nav-item"> <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-settings">Parametre</button></li> */}
                           <li className="nav-item"> <button className="nav-link" data-bs-toggle="tab" data-bs-target="#profile-change-password">Changer mot de passe</button></li>
                        </ul>
                        <div className="tab-content pt-2">
                           <div className="tab-pane fade show active profile-overview" id="profile-overview">
                              <h5 className="card-title">A propos</h5>
                              <p className="small fst-italic">{cookies.get('About')}</p>
                              <h5 className="card-title">Information sur le profile</h5>
                              <div className="row">
                                 <div className="col-lg-3 col-md-4 label ">Nom du socit</div>
                                 <div className="col-lg-9 col-md-8">{cookies.get('Name')}</div>
                              </div>
                              <div className="row">
                                 <div className="col-lg-3 col-md-4 label">Secteur d'activit</div>
                                 <div className="col-lg-9 col-md-8">{cookies.get('Secteur')}</div>
                              </div>
                              <div className="row">
                                 <div className="col-lg-3 col-md-4 label">Email</div>
                                 <div className="col-lg-9 col-md-8">{cookies.get('Email')}</div>
                              </div>
                              <div className="row">
                                 <div className="col-lg-3 col-md-4 label">Addresse</div>
                                 <div className="col-lg-9 col-md-8">{cookies.get('Adresse')}</div>
                              </div>
                              <div className="row">
                                 <div className="col-lg-3 col-md-4 label">Contact</div>
                                 <div className="col-lg-9 col-md-8">{cookies.get('Contact')}</div>
                              </div>
                              {/* <div className="row">
                                 <div className="col-lg-3 col-md-4 label">Email</div>
                                 <div className="col-lg-9 col-md-8"><a href="/cdn-cgi/l/email-protection" className="__cf_email__" data-cfemail="e982c788878d8c9b9a8687a98c91888499858cc78a8684">[email&#160;protected]</a></div>
                              </div> */}
                           </div>
                           <div className="tab-pane fade profile-edit pt-3" id="profile-edit">
                              <form >
                                 <div className="row mb-3">
                                    <label htmlFor="fullName" className="col-md-4 col-lg-3 col-form-label">Nom du socit</label>
                                    <div className="col-md-8 col-lg-9"> <input name="fullName" type="text" className="form-control" id="fullName" value={name} onChange={(e) => setName(e.target.value)} /></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="fullName" className="col-md-4 col-lg-3 col-form-label">Nom d'utilisateur</label>
                                    <div className="col-md-8 col-lg-9"> <input name="fullName" type="text" className="form-control" id="fullName" value={username} onChange={(e) => setUsername(e.target.value)} /></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="about" className="col-md-4 col-lg-3 col-form-label">A propos</label>
                                    <div className="col-md-8 col-lg-9"><input name="about" className="form-control" id="about" value={about} onChange={(e) => setAbout(e.target.value)} /></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="company" className="col-md-4 col-lg-3 col-form-label">Secteur d'activit</label>
                                    <div className="col-md-8 col-lg-9"> <input name="company" type="text" className="form-control" id="company" value={secteur} onChange={(e) => setSecteur(e.target.value)} /></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="Job" className="col-md-4 col-lg-3 col-form-label">Email</label>
                                    <div className="col-md-8 col-lg-9"> <input name="job" type="email" className="form-control" id="Job" value={email} onChange={(e) => setEmail(e.target.value)}/></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="Address" className="col-md-4 col-lg-3 col-form-label">Addresse</label>
                                    <div className="col-md-8 col-lg-9"> <input name="address" type="text" className="form-control" id="Address" value={adresse} onChange={(e) => setAdresse(e.target.value)}/></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="Phone" className="col-md-4 col-lg-3 col-form-label">Contact</label>
                                    <div className="col-md-8 col-lg-9"> <input name="phone" type="text" className="form-control" id="Phone" value={contact} onChange={(e) => setContact(e.target.value)} /></div>
                                 </div>
                                 <div className="d-grid gap-2 mt-3"> <button className="btn btn-primary" type="button">Enregistrer</button></div>
                              </form>
                           </div>

                           <div className="tab-pane fade pt-3" id="profile-change-password">
                              <form>
                                 <div className="row mb-3">
                                    <label htmlFor="currentPassword" className="col-md-4 col-lg-3 col-form-label">Ancien mot de passe</label>
                                    <div className="col-md-8 col-lg-9"> <input name="password" type="password" className="form-control" id="currentPassword" value={password} onChange={(e) => setPassword(e.target.value)} /></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="newPassword" className="col-md-4 col-lg-3 col-form-label">Nouveau mot de passe</label>
                                    <div className="col-md-8 col-lg-9"> <input name="newpassword" type="password" className="form-control" id="newPassword"/></div>
                                 </div>
                                 <div className="row mb-3">
                                    <label htmlFor="renewPassword" className="col-md-4 col-lg-3 col-form-label">Confirmation du mot de passe</label>
                                    <div className="col-md-8 col-lg-9"> <input name="renewpassword" type="password" className="form-control" id="renewPassword"/></div>
                                 </div>
                                 <div className="d-grid gap-2 mt-3"> <button className="btn btn-primary" type="button">Enregistrer</button></div>
                              </form>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </section>
        </div>
    )
}
