import axios from 'axios';
import { HttpRequestHeader } from '../helpers/utils';
export const getSalaries = () => {
    return axios.get('http://localhost:5000/feuille_temps' , {
        headers: HttpRequestHeader
    });
}

export const updateSalarie = (id, salarie) => {
    return axios.patch('http://localhost:5000/feuille_temps' + id, salarie , {
        headers: HttpRequestHeader
    });
}

export const storeSalarie = (salarie) => {
    return axios.post('http://localhost:5000/feuille_temps' , salarie , {
        headers: HttpRequestHeader
    });
}