import React, { useEffect } from 'react'
import { useState } from 'react';
import { projetdata,salariesdata,partdata } from '../../helpers/utils';
import { v4 as uuid } from 'uuid';
import { Button, TextField } from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
// import Cookies from 'universal-cookie';
// import { useState } from 'react';




export default function User() {
    const unique_id = uuid();
    const small_id = unique_id.slice(0,8)
    const [data, setData] = useState([]);
    const [projet_d, setProjet_d] = useState([]);
    const [salarie_d, setSalarie_d] = useState([]);
    const [projet, setProjet] = useState('');
    const [salarie, setSalarie] = useState('');
    const [jours, setJours] = useState('');


    useEffect(() => { 
        setProjet_d([...projetdata])
        setSalarie_d([...salariesdata])
        setData([...partdata])
    },[]);

    const onSave = () => {
        console.log('id = ',small_id)
        console.log('projet = ',projet)
        console.log('salarie = ',salarie)
    }



    return (
        <div id="main">
            <section className="participer-content">
                <div className='row'>
                    <div className='col-4'>
                        <Autocomplete
                            disablePortal
                            id="combo-box-demo"
                            options={projet_d}
                            getOptionLabel={(option) => option.lib}
                            onChange={(_, newValue) => setProjet(newValue)}
                            renderInput={(params) => 
                                <TextField     
                                {...params}
                                variant="outlined"
                                label="Libell du projet"
                                size='small'
                                fullWidth />
                            }
                        />
                    </div>
                    <div className='col-4'>
                        <Autocomplete
                            disablePortal
                            id="combo-box-demo"
                            options={salarie_d}
                            getOptionLabel={(option) => option.prenom +' '+ option.nom }
                            onChange={(_, newValue) => setSalarie(newValue)}
                            renderInput={(params) => 
                                <TextField     
                                {...params}
                                variant="outlined"
                                label="Nom du salari"
                                size='small'
                                fullWidth />
                            }
                        />
                    </div>
                    <div className='col-2'>
                    <TextField fullWidth id="outlined-basic" name="nom" label="Nombre de jours" value={jours} onChange={(e) => setJours(e.target.value)} variant="outlined" size="small"/>
                    
                    </div>
                    <div className='col-2'>
                    <Button
                        fullWidth
                        variant="contained"
                        sx={{ backgroundColor : '#012970', height:39}}
                        onClick={()=>onSave()}
                        
                    >
                        <i className='bi bi-folder-plus me-2'> </i> 
                        Enregister
                    </Button>
                    </div>
                </div>
            </section>
        </div>
    )
}
