import * as React from 'react';
import success from '../../assets/img/success.png'
import { v4 as uuid } from 'uuid';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
// import Stack from '@mui/material/Stack';
import { useState } from 'react';
// import { storeSalarie } from '../../services/salarie.service';
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material';
import { salariesdata } from '../../helpers/utils';
import AddCardIcon from '@mui/icons-material/AddCard';
import ArrowCircleRightIcon from '@mui/icons-material/ArrowCircleRight';
import ArrowCircleLeftIcon from '@mui/icons-material/ArrowCircleLeft';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import Cookies from 'universal-cookie';

// import AutoModeIcon from '@mui/icons-material/AutoMode';


const steps = ['Bulletin de salaire' , 'Information', 'Diplme', 'Fonction' , 'Autre' , 'Pices justificatives' ];

export default function Stepperforme() {
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());

  const cookies = new Cookies();

  const [prenom, setPrenom] = useState('');
  const [adresse, setAdresse] = useState('');
  const [contact, setContact] = useState('');
  const [nom, setNom] = useState('');
  const [diplome, setDiplome] = useState('');
  const [etablissement, setEtablissement] = useState('');
  const [annee, setAnnee] = useState('');
  const [fonction, setFonction] = useState('');
  const [brut, setBrut] = useState('');
  const [cotisation, setCotisation] = useState('');
  const [cadre, setCadre] = useState('');
  const [cheure, setCheure] = useState('');
  const [entrer, setEntrer] = useState('');
  const [sortie, setSortie] = useState('');
  const [ticket, setTicket] = useState('');

  const [salaries] = useState(salariesdata);

  const unique_id = uuid();
  const small_id = unique_id.slice(0,8)


  const onSubmit = (e) => {
    // setSalaries(salariesdata)
    // e.preventDefault();
      let salarie = {
        id : small_id,
        nom : nom,
        prenom : prenom,
        adresse : adresse,
        contact : contact,
        diplome : diplome,
        etablissement : etablissement,
        annee : annee,
        fonction : fonction,
        cadre : cadre,
        brut : brut,
        cotisation : cotisation,
        cotisationeligible : ((cotisation * 20) / 100).toFixed(2),
        salairecharge : (cotisation * 20) / (100 * brut),
        cheure : cheure,
        cjours : (cheure / 24).toFixed(2),
        entrer : entrer,
        sortie : sortie,
        ticket : ticket,
        user_id : cookies.get('Id')
      };
      salaries.push(salarie)
      console.log('salarie ajouter',salarie)
      salariesdata.concat(salaries);
      console.log('Liste salaris' , salariesdata)
      // storeSalarie(salarie)
      // .then((result) => {
      //   return result.data
      // })
  }

  const reset = () => {
    setNom('');
    setPrenom('');
    setAdresse('');
    setContact('');
    setDiplome('');
    setEtablissement('');
    setAnnee('');
    setFonction('');
    setBrut('');
    setCadre('');
    setCotisation('');
    setCheure('');
    setEntrer('');
    setSortie('');
    setTicket('');
  }

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
    reset();
  };

  const valide = () => {
    if(activeStep === steps.length - 1) {
      onSubmit()
      handleNext()
    } else {
      handleNext()
    }
  }

  return (
    <div id='main'>
      <div className='ajout-salarie-content'>
        <div className='ajout-salarie-content-item text-center'>
          <span className='ajout-salarie-content-item-text'>Ajout d'un nouveau salari</span><br/>
        <Stepper activeStep={activeStep} alternativeLabel sx={{marginTop: 3}}>
          {steps.map((label, index) => {
            return (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <br/>
        {activeStep === steps.length ? (
          <React.Fragment>
            <Typography sx={{ mt: 1, mb: 1, fontSize:24 }}>
              Salari ajouter avec succs
            </Typography>
            <div className='d-flex justify-content-center'>
              <div>
                <Button onClick={handleReset} variant="contained" sx={{ backgroundColor : '#012970'}} startIcon={<AddCardIcon />} >Nouveau</Button><br/>
                <img src={success} alt="success" width='45%'/>
              </div>
            </div>
          </React.Fragment>
        ) : (
          <form onSubmit={(e) => onSubmit(e)}>
          <React.Fragment>
            {activeStep === 0 && (
              <div>
                <TextField
                  focused
                  margin='normal'
                  id="outlined-basic"
                  label="Importer le bulletin de salaire de l'employ"
                  variant="outlined"
                  type="file"
                  accept="pdf/*"
                />
              </div>
            )}
            {activeStep === 1 && (
              <div>
                <div className='row'>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" name="nom" label="Nom" variant="outlined"  value={nom} onChange={(e) => setNom(e.target.value)}/>
                    <br/>
                    <TextField fullWidth id="outlined-basic" name="adresse" label="Adresse" variant="outlined" margin="normal" value={adresse} onChange={(e) => setAdresse(e.target.value)}/>
                  </div>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" label="Prnom(s)" variant="outlined" value={prenom} onChange={(e) => setPrenom(e.target.value)} />
                    <TextField fullWidth id="outlined-basic" name="contact" label="Contact" variant="outlined" margin="normal" value={contact} onChange={(e) => setContact(e.target.value)}/>
                  </div>
                </div>
              </div>
            )}
            {activeStep === 2 && (
              <div>
                <div className='row'>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" label="Diplme le plus lev" variant="outlined" value={diplome} onChange={(e) => setDiplome(e.target.value)} />
                    <TextField fullWidth id="outlined-basic" label="Anne obtention" margin="normal" variant="outlined" value={annee} onChange={(e) => setAnnee(e.target.value)} />
                  </div>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" label="Etablissement" variant="outlined" value={etablissement} onChange={(e) => setEtablissement(e.target.value)} />
                  </div>
                </div>
              </div>
            )}
            {activeStep === 3 && (
              <div>
                <div className='row'>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" label="Salaire brut" variant="outlined" value={brut} onChange={(e) => setBrut(e.target.value)}/>
                    <TextField fullWidth margin="normal" id="outlined-basic" label="Fonction dans l'entreprise" variant="outlined" value={fonction} onChange={(e) => setFonction(e.target.value)} />
                    </div>
                  <div className='col-6'>
                  <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">Employ cadre</InputLabel>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={cadre}
                        label="Employ cadre"
                        onChange={(e) => setCadre(e.target.value)}
                        
                        // sx={{width : 200}}
                      >
                        <MenuItem value='oui'>Oui</MenuItem>
                        <MenuItem value='non'>Non</MenuItem>
                      </Select>
                    </FormControl>
                    <TextField fullWidth margin='normal' id="outlined-basic" label="Cotisations patronales totales" variant="outlined" value={cotisation} onChange={(e) => setCotisation(e.target.value)}/>
                  </div>
                </div>
              </div>
            )}
            {activeStep === 4 && (
              <div>
                <div className='row'>
                  <div className='col-6'>
                    <TextField fullWidth id="outlined-basic" label="Cumul temps (en heures)" variant="outlined" value={cheure} onChange={(e) => setCheure(e.target.value)} />
                    <TextField
                      fullWidth
                      id="date"
                      label="Entrer"
                      type="date"
                      margin='normal'
                      value={entrer} 
                      onChange={(e) => setEntrer(e.target.value)}
                      InputLabelProps={{
                        shrink: true,
                      }}
                    />
                  </div>
                  <div className='col-6'>
                  <TextField fullWidth id="outlined-basic" label="Part/Int & Tickets restaurants" variant="outlined" value={ticket} onChange={(e) => setTicket(e.target.value)} />
                    <TextField
                      fullWidth
                      id="date"
                      label="Sortie"
                      type="date"
                      margin='normal'
                      value={sortie} 
                      onChange={(e) => setSortie(e.target.value)}
                      InputLabelProps={{
                        shrink: true,
                      }}
                    />
                  </div>
                </div>
              </div>
            )}
            {activeStep === 5 && (
              <div>
                <TextField
                  focused
                  id="outlined-basic"
                  label="Importer le diplome plus lev de l'employ"
                  variant="outlined"
                  type="file"
                  accept="pdf/*"
                />
                <br/>

                <TextField
                  focused
                  margin='normal'
                  id="outlined-basic"
                  label="Importer le CV de l'employ"
                  variant="outlined"
                  type="file"
                  accept="pdf/*"
                />
              </div>
            )}
            <Box sx={{ display: 'flex', flexDirection: 'row', justifyContent:'center', pt: 2 }}>
              <Button
                color="primary"
                disabled={activeStep === 0}
                onClick={handleBack}
                variant="outlined"
                startIcon={<ArrowCircleLeftIcon />}
                sx={{ mr: 1 }}
              >
                Prcedent
              </Button>

              <Button onClick={valide} variant={activeStep === steps.length - 1 ? 'contained' : 'outlined'} color="primary" endIcon={activeStep === steps.length - 1 ? <CheckCircleIcon/> : <ArrowCircleRightIcon />}>
                {activeStep === steps.length - 1 ? 'Terminer' : 'Suivant'}
              </Button>
            </Box>

          </React.Fragment>
          </form>
        )}
        </div>
      </div>
    </div>
  );
}