import React from 'react'
import { getUser } from "../../services/login.service";
import logo from '../../assets/img/logo.png';
import '../../assets/css/style.css'
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { Link , useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { useState } from 'react';
import Cookies from 'universal-cookie';
import { FormControl, IconButton, InputAdornment, InputLabel, OutlinedInput, TextField } from '@mui/material';
import { Visibility, VisibilityOff } from '@mui/icons-material';

export default function Signin() {
    const cookies = new Cookies();
    const navigate = useNavigate();

    const[user, setUser] = useState([]);
    const[username, setUsername] = useState('')
    const[password, setPassword] = useState('')
    const[errorname, setErrorname] = useState(false)
    const[errorpassword, setErrorpassword] = useState(false)
    // const[link, setLink] = useState(true)

    const [showPassword, setShowPassword] = React.useState(false);

    const handleClickShowPassword = () => setShowPassword((show) => !show);
  
    const handleMouseDownPassword = (event) => {
      event.preventDefault();
    };


    useEffect(() => {
        loadUser();
    },[]);

    const setCookie = (id, username, role, name, secteur, about, email, contact, adresse, password) => {
        cookies.set('Id', id, { path: '/' })
        cookies.set('Username', username, { path: '/' })
        cookies.set('Password', password, { path: '/' })
        cookies.set('Name', name, { path: '/' })
        cookies.set('Secteur', secteur, { path: '/' })
        cookies.set('About', about, { path: '/' })
        cookies.set('Email', email, { path: '/' })
        cookies.set('Contact', contact, { path: '/' })
        cookies.set('Adresse', adresse, { path: '/' })
        cookies.set('Role', role, { path: '/' })
        navigate("/navbar/dashboard-client");
    }

    const checkUser = (username, password, data) => {
        console.log('mandea')
        var index = data.findIndex((e) => {
            return e.username === username;
        });
        if (index !== -1) {
            setErrorname(false)
            data[index].password === password ? (setCookie(data[index].id, username, data[index].role, data[index].name, data[index].secteur, data[index].about, data[index].email, data[index].contact, data[index].adresse,data[index].password) ) : setErrorpassword(true);
        } else {
            navigate("/");
            setErrorname(true)
        }
    }

    const loadUser = () => {
        getUser()
        .then((result) => {
            if (result.status === 200) {
                setUser(result.data);
            }
        })
        .catch((error) => {
            console.log(error);
        });
    }


  return (
    <div className="container">
          <div className="d-flex justify-content-center">
            <div className="login-content">
                <div className="d-flex justify-content-center">
                    <img src={logo} alt="logo" width="150px"/>
                </div>
                <br/>
                <div className='card-content'>
                  <div className="card-login">
                    <div className="card-head text-center">
                        <span className="login-title">AUTHENTIFICATION</span><br/>
                        <span className="login-text">Veuillez vous connectez pour accder aux plateforme</span>
                    </div>
                    <div className="form-login">
                        <TextField fullWidth error={errorname===false ? false : true} size='small' id="outlined-basic" name="nom" label="Nom d'utilisateur" variant="outlined"  value={username} onChange={(e) => setUsername(e.target.value)}/>
                        <span className={errorname===false ? 'd-none' : 'error'}>Le nom d'utilisateur n'existe pas!</span><br/>
                        
                        <FormControl fullWidth sx={{marginTop : 3}} size='small' variant="outlined" error={errorpassword===false ? false : true}>
                            <InputLabel htmlFor="outlined-adornment-password">Mot de passe</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-password"
                                type={showPassword ? 'text' : 'password'}
                                endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                    >
                                    {showPassword ? <VisibilityOff /> : <Visibility />}
                                    </IconButton>
                                </InputAdornment>
                                }
                                label="Mot de passe"
                                value={password} onChange={(e) => setPassword(e.target.value)}
                            />
                        </FormControl>
                        <span className={errorpassword===false ? 'd-none' : 'error'}>Le mot de passe est incorrect!</span>
                        

                        <div className="row login-question">
                            
                            <div className="col-lg-6 col-md-6 col-sm-12 d-flex justify-content-start">
                                <span className="text-mdp"><input type="checkbox" className="form-check-input" value=""/> Se souvenir de moi</span> 
                            </div>
                            <div className="col-lg-6 col-md-6 col-sm-12 d-flex justify-content-end">
                                <span className="lost-mdp"><a href="test">Mot de passe oubli?</a></span> 
                            </div>
                        </div>
                        <div className="button-con">
                        <button className="btn-connex" onClick={() => checkUser(username, password, user)} >Se connecter</button>
                        </div>
                        <div className="login-footer text-center">
                            <span className="text-footer">Pas de compte? </span>
                            <span className="link-footer"><Link to='/signup'> S'inscrire</Link></span>
                        </div>

                    </div>
                </div>
                </div>
                
            </div>
        </div>
      </div>
  )
}
