import React, { useState } from 'react'
import '../../assets/css/style.css'
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { Link, useNavigate } from 'react-router-dom';
import { FormControl, IconButton, InputAdornment, InputLabel, OutlinedInput, TextField } from '@mui/material';
import { storeUser } from "../../services/login.service";
import { Visibility, VisibilityOff } from '@mui/icons-material';


export default function Signup() {

    const [name, setName] = useState('');
    const [secteur, setSecteur] = useState('');
    const [about, setAbout] = useState('');
    const [email, setEmail] = useState('');
    const [contact, setContact] = useState('');
    const [adresse, setAdresse] = useState('');
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [confirmation, setConfirmation] = useState('');
    const [error, setError] = useState(false)

    const navigate = useNavigate();

    const [showPassword, setShowPassword] = React.useState(false);
    const [showConfirm, setShowConfirm] = React.useState(false);

    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleClickShowConfirm = () => setShowConfirm((show) => !show);
  
    const handleMouseDownPassword = (event) => {
      event.preventDefault();
    };

    const handleMouseDownConfirm = (event) => {
        event.preventDefault();
      };

    const onStore = (e) => {
        let user = {
            id : 0,
            username : username,
            name : name,
            secteur : secteur,
            about : about,
            email : email,
            contact : contact,
            adresse : adresse,
            password : password,
            role : 'client'
        };
        password === confirmation ? (
            storeUser(user)
            .then((result) => {
                navigate('/')
                return result.data
            })
        ) : (
            setError(true)
        )
    }


  return (
    <div className="container">
        <div className="d-flex justify-content-center">
          <div className="inscription-content">
                <div className="inscription-card mb-2">
                    <div className="row d-flex justify-content-center">
                        <div className="col-lg-6 col-md-12 col-sm-12">
                            <div className='img-carde'>
                            <div className='d-flex justify-content-center align-items-center text-image'>
                                <div className='text-center'>
                                    {/* <h1 className='titre-txt'>INNOVANALYSE</h1>
                                    <h3 className='desc-txt'>Cration d'un compte</h3> */}
                                </div>
                            </div>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-12 col-sm-12 form-carde">
                            <div className="formulaire">
                                <div className="form-text-content text-center">
                                    <span className="form-create-title">CREER UN COMPTE</span><br/>
                                    <span className="form-create-substitle">Veuillez remplir les champs suivants</span><br/>
                                    <div className="d-flex justify-content-center mt-2 mb-2"><div className="form-create-line"></div></div>
                                </div>
                                <TextField fullWidth size='small' id="outlined-basic" name="nom" label="Nom du socit" variant="outlined"  value={name} onChange={(e) => setName(e.target.value)}/>
                                <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="Secteur d'activit" variant="outlined"  value={secteur} onChange={(e) => setSecteur(e.target.value)}/> 
                                <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="A propos" variant="outlined"  value={about} onChange={(e) => setAbout(e.target.value)}/> 
                                <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="Email" variant="outlined"  value={email} onChange={(e) => setEmail(e.target.value)}/> 
                                <div className="row">
                                    <div className="col-lg-6 col-md-6 col-sm-12">
                                        <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="Contact" variant="outlined"  value={contact} onChange={(e) => setContact(e.target.value)}/>
                                    </div>
                                    <div className="col-lg-6 col-md-6 col-sm-12">
                                        <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="Adresse" variant="outlined"  value={adresse} onChange={(e) => setAdresse(e.target.value)}/> 
                                    </div>
                                </div>
                                {/* <span>Authentification info</span> */}
                                <TextField fullWidth sx={{marginTop : 1.7}} size='small' id="outlined-basic" name="nom" label="Nom d'utilisateur" variant="outlined"  value={username} onChange={(e) => setUsername(e.target.value)}/> 
                                <div className="row">
                                    <div className="col-lg-6 col-md-6 col-sm-12">
                                        <FormControl fullWidth sx={{marginTop : 1.7}} size='small' variant="outlined" error={error===false ? false : true}>
                                            <InputLabel htmlFor="outlined-adornment-password">Mot de passe</InputLabel>
                                            <OutlinedInput
                                                id="outlined-adornment-password"
                                                type={showPassword ? 'text' : 'password'}
                                                endAdornment={
                                                <InputAdornment position="end">
                                                    <IconButton
                                                    aria-label="toggle password visibility"
                                                    onClick={handleClickShowPassword}
                                                    onMouseDown={handleMouseDownPassword}
                                                    edge="end"
                                                    >
                                                    {showPassword ? <VisibilityOff /> : <Visibility />}
                                                    </IconButton>
                                                </InputAdornment>
                                                }
                                                label="Mot de passe"
                                                value={password} onChange={(e) => setPassword(e.target.value)}
                                            />
                                        </FormControl>
                                        <span className={error===false ? 'd-none' : 'error'}>Verifier votre mot de passe!</span>
                                    </div>
                                    <div className="col-lg-6 col-md-6 col-sm-12">
                                        <FormControl fullWidth sx={{marginTop : 1.7}} size='small' variant="outlined" error={error===false ? false : true}>
                                                <InputLabel htmlFor="outlined-adornment-password">Confirmation</InputLabel>
                                                <OutlinedInput
                                                    id="outlined-adornment-password"
                                                    type={showConfirm ? 'text' : 'password'}
                                                    endAdornment={
                                                    <InputAdornment position="end">
                                                        <IconButton
                                                        aria-label="toggle password visibility"
                                                        onClick={handleClickShowConfirm}
                                                        onMouseDown={handleMouseDownConfirm}
                                                        edge="end"
                                                        >
                                                        {showConfirm ? <VisibilityOff /> : <Visibility />}
                                                        </IconButton>
                                                    </InputAdornment>
                                                    }
                                                    label="Confirmation"
                                                    value={confirmation} onChange={(e) => setConfirmation(e.target.value)}
                                                />
                                            </FormControl>
                                    </div>
                                </div>
                                <span className="text-condition"><input type="checkbox" className="form-check-input mt-2" value=""/>&ensp;J'accepte les termes et conditions d'innovanalyse</span> 

                                <div className="button-create">
                                    <button className="btn-create" onClick={() => onStore()}>S'inscrire</button>
                                </div>
                                <div className='text-center signin-link'>
                                    <span className=''><Link to='/'>Dja un compte?</Link></span>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>

  )
}
