import { Button, TextField } from '@mui/material';
import { MDBCard, MDBCardBody, MDBCardHeader, MDBCardText } from 'mdbreact';
import React, { useEffect } from 'react'
import { useState } from 'react';
import { projetdata } from '../../helpers/utils';
import { v4 as uuid } from 'uuid';
import Cookies from 'universal-cookie';
// import Cookies from 'universal-cookie';
// import { useState } from 'react';


export default function User() {
    const unique_id = uuid();
    const small_id = unique_id.slice(0,8)
    const cookies = new Cookies();

    const [data, setData] = useState([]);
    const [id, setId] = useState('');
    const [lib, setLib] = useState('');
    const [nom, setNom] = useState('');

    useEffect(() => { 
        setData([...projetdata])
    },[]);

    const onSubmit = (e) => {
        let projet = {
            id : small_id,
            lib : lib,
            nom : nom,
            user_id : cookies.get('Id')
        };
        projetdata.push(projet);
        setData([...projetdata]);
        console.log('projetdata' , projetdata)
        console.log('data' , data)
        onReset();
    }

    const onRemove =(id) =>{
        console.log(id)
        var index = projetdata.findIndex((e) => {
			return e.id === id;
		});
        if (index !== -1) {
            projetdata.splice(index, 1);
        //   console.log('array : '+array)
          setData([...projetdata]);
          console.log('salarie data : '+projetdata)
        }
    }

    const onReset = () => {
        setLib('');
        setNom('');
      }

    const onOpenModal = (id) =>{
        console.log(id)
        var index = projetdata.findIndex((e) => {
			return e.id === id;
		});
        if (index !== -1) {
            console.log(projetdata[index])
            setId(projetdata[index].id)
            setLib(projetdata[index].lib)
            setNom(projetdata[index].nom)
        }
    }

    const onUpdate = (id) => {
        onRemove(id);
        onSubmit();
    }

    return (
        <div id="main">
            <section className="projet-content">
                <div className='text-center'>
                    <span className='ajout-projet-content-item-text'>Ajout d'un nouveau projet</span>
                </div>
                <div className='d-flex justify-content-center projet-form'> 
                    <TextField sx={{ mr: 1}} size='small' id="outlined" name="nom" label="Libell du projet" variant="outlined"  value={lib} onChange={(e) => setLib(e.target.value)}/>
                    <TextField fullWidth size='small' id="outlined" name="nom" label="Nom du projet" variant="outlined"  value={nom} onChange={(e) => setNom(e.target.value)}/>
                    <Button
                        variant="contained"
                        sx={{ ml: 1, backgroundColor : '#012970'}}
                        onClick={()=>onSubmit()}
                    >
                        <i className='bi bi-folder-plus me-1'> </i> 
                        Ajouter
                    </Button>

                </div>
                <hr/>
                <div className='projet-liste'>
                    <div className='row'>
                    {data.length == null || data.length === 0 ? (
                        <div className='text-vide text-center'>
                            <h2>Vous n'avez pas encore entrer des projet!</h2>
                            {/* <img src={vide} alt="no data" className='nodata-img' width='40%'/> */}
                        </div> ) : (data.map(e => (
                        <div className='col-4'>
                            <MDBCard>
                                <MDBCardBody>
                                    <MDBCardHeader>
                                        <span className='close-list'> 
                                            <i className='close-list-item bi bi-pencil' onClick={() => onOpenModal(e.id)} data-bs-toggle="modal" data-bs-target="#ExtralargeModal"></i> 
                                            <span className='projet-lib'>{e.lib}</span> 
                                            <i className='close-list-item bi bi-x-circle-fill' onClick={()=>onRemove(e.id)}></i>
                                        </span>  
                                    </MDBCardHeader>
                                    <MDBCardText muted className='text-center'>
                                        <span className='projet-name'>{e.nom}</span>
                                    </MDBCardText>
                                </MDBCardBody>
                            </MDBCard>
                        </div> 
                        )))
                    }
                    </div>
                </div>
                <div className="modal fade" id="ExtralargeModal" tabIndex="-1">
                    <div className="modal-dialog modal-xl modal-dialog-scrollable">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">Information sur le salari</h5>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body"> 
                                <div className='row'>
                                    <div className='col-sm-12 col-md-3 col-lg-3'>
                                        <TextField fullWidth margin="normal" id="outlined-basic" name="lib" label="Libell du projet" value={lib} onChange={(e) => setLib(e.target.value)} variant="outlined" size="small"/>
                                    </div>
                                    <div className='col-sm-12 col-md-9 col-lg-9'>
                                        <TextField fullWidth margin="normal" id="outlined-basic" name="nom" label="Nom du projet" value={nom} onChange={(e) => setNom(e.target.value)} variant="outlined" size="small"/>
                                    </div>
                                    
                                </div>
                            </div>
                            <div className="modal-footer"> <button type="button" className="btn btn-secondary" onClick={()=>onReset()} data-bs-dismiss="modal"><i className='bi bi-reply-all-fill'> </i> Annuler</button> <button type="button" className="btn btn-primary" data-bs-dismiss="modal" onClick={() => onUpdate(id)}><i className='bi bi-check2-circle'> </i> Enregister</button></div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}
