import './App.css';
import { Route, Routes } from 'react-router-dom';
import Signin from './Components/login/Signin';
import Signup from './Components/login/Signup';
import Navbar from './Components/navbar/Navbar';
import Projet from './Components/projet/Projet';
import Participer from './Components/participation/Participer'
import Salarie from './Components/salarie/Salarie';
import Client from './Components/dashboard/Client';
import Contact from './Components/contact/Contact';
import User from './Components/utilisateur/User';
// import Addsalarie from './Components/salarie/Addsalarie';
import Stepperforme from './Components/steppers/Stepperforme';

function App() {
  
  return (
    <div className="App">
      <Routes>
        <Route path='/' element={ <Signin /> }/>
        <Route path='/signup' element={ <Signup /> }/>
        <Route path='/navbar' element={ <Navbar /> }>
          <Route path='/navbar/dashboard-client' element={ <Client /> }/>
          <Route path='/navbar/projet' element={ <Projet /> }/>
          <Route path='/navbar/salarie' element={ <Salarie /> }/>
          <Route path='/navbar/participer' element={ <Participer /> }/>
          <Route path='/navbar/addsalarie' element={ <Stepperforme /> }/>
          <Route path='/navbar/contact' element={ <Contact /> }/>
          <Route path='/navbar/user' element={ <User /> }/>
        </Route>
      </Routes>
    </div>

  );
}

export default App;

